import { row, col, css } from "../utils";

class Block {
  constructor(value, options) {
    // this.type = type;
    this.value = value;
    this.options = options;
  }
  toHTML() {
    throw new Error("–ú–µ—Ç–æ–¥ toHTML –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω");
  }
}

export class TitleBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }
  toHTML() {
    const { tag = "h1", styles } = this.options;
    return row(col(`<${tag}>${this.value}</${tag}>`), css(styles));
  }
}

export class TextBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }
  toHTML() {
    return row(col(`<p>${this.value}</p>`), css(this.options.styles));
  }
}

export class ColumsBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }
  toHTML() {
    const html = this.value.map((item) => col(item)).join("");
    return row(html, css(this.options.styles));
  }
}

export class ImageBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }
  toHTML() {
    const { imageStyles: is, styles } = this.options;
    return row(`<img src="${this.value}" style="${css(is)}" />`, css(styles));
  }
}
